function p(text) {
  return { text };
}

function bold(text) {
  return { classList: "bold center", text };
}

function center(text) {
  return { classList: "center", text };
}

function yes(text) {
  return { classList: "yes", text };
}

function no(text) {
  return { classList: "no", text };
}

